import React from 'react';

const FilterTop = () => {
  return (
    <div className='d-flex justify-content-start pt-2 pb-4  me-4 align-items-center border-bottom border-secondary'>
      <h4 className='text-primary px-5 ms-3 me-2 m-0 border-start border-2 border-primary '>
        الفلاتر
      </h4>
      <div className='btn-group px-4 ' role='group' aria-label='Basic example'>
        <button
          type='button'
          className='btn btn-secondary  text-white fs-6 fw-bold mx-3 px-5'
        >
          الاعلانات المرفقة بفديوهات
        </button>
        <button
          type='button'
          className='btn btn-primary fs-6 fw-bold mx-3 px-5'
        >
          الاعلانات المرفقة بصور
        </button>
      </div>
      <h5 className='text-primary px-3 border-end border-2 border-primary m-0'>
        صنف حسب:
      </h5>
      <select className='form-select w-25 ' aria-label='Default select example'>
        <option value='1'>المدرجة حديثا</option>
        <option value='2'>الأكثر صلة</option>
        <option value='3'>الأعلي سعرا</option>
        <option value='4'>الأقل سعرا</option>
      </select>
    </div>
  );
};

export default FilterTop;
