import React from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation, Scrollbar } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import 'swiper/css/scrollbar';

//link
import { Link } from 'react-router-dom';
import { useMainContext } from '../../contexts/MainProvider';

const Navbar = () => {
  const { openSubmenu, closeSubmenu } = useMainContext();
  const displaySubmenu = (e) => {
    const page = e.target.textContent;
    const tempBtn = e.target.getBoundingClientRect();
    console.log(tempBtn);
    const center = (tempBtn.left + tempBtn.right) / 2;
    const bottom = tempBtn.bottom - 3;
    openSubmenu(page, { center, bottom });
  };
  const handleSubmenu = (e) => {
    if (!e.target.classList.contains('link-nav')) {
      closeSubmenu();
    }
  };
  return (
    <div
      className='mynavbar w-100 text-center bg-white  border-top border-2 position-relative px-5'
      onMouseOver={handleSubmenu}
    >
      <Swiper
        spaceBetween={30}
        slidesPerView={6}
        navigation
        breakpoints={{
          0: {
            // width: 576,
            slidesPerView: 1,
          },
          768: {
            // width: 768,
            slidesPerView: 2,
          },
          992: {
            // width: 768,
            slidesPerView: 4,
          },
          1200: {
            // width: 768,
            slidesPerView: 6,
          },
        }}
        modules={[Navigation, Scrollbar]}
        className='mySwiper '
      >
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            سيارات
          </Link>
        </SwiperSlide>
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            عربيات
          </Link>
        </SwiperSlide>
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            موتوسكيلات
          </Link>
        </SwiperSlide>
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            موتوسكيلات
          </Link>
        </SwiperSlide>
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            موتوسكيلات
          </Link>
        </SwiperSlide>
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            موتوسكيلات
          </Link>
        </SwiperSlide>
        <SwiperSlide className='link-nav py-4'>
          <Link
            className='link-nav text-primary pointer fs-5 text-decoration-none'
            onMouseOver={displaySubmenu}
            to='/sa/as'
          >
            موتوسكيلات
          </Link>
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Navbar;
