import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import ShareLayout from './components/shared-components/ShareLayout';

// All PAges
import EditProfile from './pages/EditProfile';
import FilterAds from './pages/FilterAds';
import Help from './pages/Help';
import Home from './pages/Home';
import Profile from './pages/Profile';
import PuplishProduct from './pages/PuplishProduct';
import Sell from './pages/Sell';
import Seller from './pages/Seller';
import Settings from './pages/Settings';
import SingleProduct from './pages/SingleProduct';
import Wallet from './pages/Wallet';

function App() {
  return (
    <div className='App'>
      <BrowserRouter>
        <Routes>
          <Route path='/' element={<ShareLayout />}>
            <Route index element={<Home />}></Route>
            <Route
              path='singleproduct/:productid'
              element={<SingleProduct />}
            ></Route>
            <Route path='help' element={<Help />}></Route>
            <Route path='seller/:sellerid' element={<Seller />}></Route>
            <Route
              path='/:category/:subcategory'
              element={<FilterAds />}
            ></Route>
            <Route path='/:category' element={<FilterAds />}></Route>
          </Route>
          <Route path='profile' element={<ShareLayout />}>
            <Route path='myads' element={<Profile />} />
            <Route path='wallet' element={<Wallet />} />
            <Route path='editprofile' element={<EditProfile />} />
            <Route path='settings' element={<Settings />} />
          </Route>
          <Route path='sell' element={<ShareLayout />}>
            <Route index element={<Sell />} />
            <Route path='puplish' element={<PuplishProduct />} />
            {/*
            <Route path='editprofile' element={<EditProfile />} /> */}
          </Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
