import React, { useState } from 'react';
import BasicInfo from '../components/edit-profile/BasicInfo';
import IdentityDocumented from '../components/edit-profile/IdentityDocumented';

const EditProfile = () => {
  const [showIdentity, setShowIdentity] = useState(false);

  return (
    <section className='edit-profile bg-primary p-4 text-white m-4 rounded'>
      <div className='container-fluid'>
        <div className='box border-bottom pb-4'>
          <h4>تعديل الملف الشخصي</h4>
        </div>
        {!showIdentity && <BasicInfo setShowIdentity={setShowIdentity} />}
        {showIdentity && <IdentityDocumented />}
      </div>
    </section>
  );
};

export default EditProfile;
