import React from 'react';

const MySettings = () => {
  return (
    <div className='my-settings  my-5'>
      <div className='container-fluid'>
        <div className='row'>
          <div className='col-md-2'>
            <h3 className='text-primary'>الإعدادات</h3>
          </div>
          <div className='col-md-10'>
            {/* Box1 */}
            <div className='box1 bg-primary py-4 rounded'>
              <div className='box border-bottom pb-4'>
                <h4 className='px-2 text-white'>خصوصية إعلاناتي</h4>
              </div>
              {/* CheckBOx */}
              {/* Checkbox */}
              <div className='my-4 mx-2 radio-group row gy-3'>
                <div className=' col-xl-2 col-lg-3 col-md-4'>
                  <div className='bg-light rounded radio-contact mt-3 mt-md-0 py-3 px-2 '>
                    <label className='radio text-dark '>
                      <input type='radio' value='female' name='contact' />
                      رقم الموبايل
                      <span></span>
                    </label>
                  </div>
                </div>
                <div className=' col-xl-2 col-lg-3 col-md-4'>
                  <div className='bg-light rounded radio-contact mt-3 mt-md-0 py-3 px-2 '>
                    <label className='radio text-dark '>
                      <input type='radio' value='female' name='contact' />
                      أدس شات
                      <span></span>
                    </label>
                  </div>
                </div>
                <div className=' col-xl-2 col-lg-3 col-md-4'>
                  <div className='bg-light rounded radio-contact mt-3 mt-md-0 py-3 px-2 '>
                    <label className='radio text-dark '>
                      <input type='radio' value='female' name='contact' />
                      واتساب
                      <span></span>
                    </label>
                  </div>
                </div>
                <div className=' col-xl-2 col-lg-3 col-md-4'>
                  <div className='bg-light rounded radio-contact mt-3 mt-md-0 py-3 px-2 '>
                    <label className='radio text-dark '>
                      <input type='radio' value='female' name='contact' />
                      الثلاثة
                      <span></span>
                    </label>
                  </div>
                </div>
              </div>
              {/* CheckBOx */}
            </div>
            {/* Box1 */}
            {/* Box 2 */}
            <div className='box1 bg-primary py-4 rounded mt-2'>
              <div className='box border-bottom pb-4'>
                <h4 className='px-2 text-white'>تغيير كلمة المرور</h4>
              </div>
              <div className='form mx-2 row'>
                <div className='col-sm-9 col-12'>
                  <input
                    className='form-control form-control-lg mt-4 '
                    type='text'
                    placeholder='كلمةالمرور الحالية'
                    checked
                  />
                </div>
                <div className='col-sm-9 col-12'>
                  <input
                    className='form-control form-control-lg mt-4 '
                    type='text'
                    placeholder='كلمة المرور الجديدة'
                  />
                </div>
                <div className='col-sm-9 col-12'>
                  <input
                    className='form-control form-control-lg mt-4 '
                    type='text'
                    placeholder='تأكيد كلمةالمرور الجديدة'
                  />
                </div>
                <div className='col-12 text-center mt-5'>
                  <button className='btn btn-secondary btn-lg text-white'>
                    تغيير كلمة المرور
                  </button>
                </div>
              </div>
            </div>
            {/* Box 2 */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default MySettings;
