import React from 'react';
import w from '../../assets/ICONs/w.png';
import whitearrowleft from '../../assets/ICONs/whitearrowleft.png';
import phone from '../../assets/ICONs/phone.png';
import personimg from '../../assets/imgs/person.png';

const SellerDetails = () => {
  return (
    <div className='seller-details'>
      <div className='box bg-primary py-4 px-3 text-white rounded'>
        <div className='d-flex flex-column flex-sm-row justify-content-between'>
          <h4 className='fw-bold'>جاكيت رجالي</h4>
          <h4 className='text-secondary'>
            <span>100</span>
            <span className='mx-2'>L.E</span>
          </h4>
          <img src={w} alt='w' className='icon' />
        </div>
        <div className='d-flex flex-column flex-sm-row justify-content-between mt-3 mt-sm-5'>
          <h6 className='fw-light'>اسكندرية مصر</h6>
          <h6 className='fw-light'>100 مشاهدة</h6>
          <h6 className='fw-light'>منذ خمس ساعات</h6>
        </div>
      </div>

      <div className='box bg-primary py-4 px-3 text-white mt-3 rounded'>
        <div className='d-flex justify-content-start'>
          <h4 className='fw-bold'>وصف البائع</h4>
        </div>

        <div className='d-flex  justify-content-between mt-4 '>
          <div className='d-flex flex-column flex-sm-row justify-content-start'>
            <img src={personimg} alt='icon' className='avatar rounded-circle' />
            <div className='mx-0 mt-2 mt-sm-0 mx-sm-3'>
              <h5 className='fw-bold'>ذياد فؤاد</h5>
              <h6>
                <span> عضو منذ </span>
                <span className='fw-lighter'>2090</span>
              </h6>
              <div className='d-flex flex-column flex-sm-row align-items-center mt-3'>
                <img src={phone} alt='phone' className='icon' />
                <span className='fs-3 fw-bold me-0 me-sm-3'>.. .... ....</span>
                <h4 className='show-num text-decoration-underline pointer me-0 me-sm-3'>
                  اظهار الرقم
                </h4>
              </div>
            </div>
          </div>
          <img
            src={whitearrowleft}
            alt='whitearrowleft '
            className='align-self-center'
          />
        </div>

        <div className='d-flex justify-content-center mt-3 '>
          <button className='btn btn-secondary text-white fs-5 '>
            تواصل مع البائع
          </button>
        </div>
      </div>
    </div>
  );
};

export default SellerDetails;
